export const convertToTreeSelectData = (items: any[]): any => {
	return items.reduce((acc, curr) => {
		if (curr.sub_departments.length > 0) {
			while (curr.sub_departments.length > 0) {
				return [
					...acc,
					{
						title: curr['name'],
						key: curr['id'],
						value: curr['id'],
						children: convertToTreeSelectData(curr.sub_departments)
					}
				];
			}
		} else {
			return [
				...acc,
				{
					title: curr['name'],
					key: curr['id'],
					value: curr['id']
				}
			];
		}
	}, []);
};
