import React from 'react';
import EntityForm from 'modules/entity/forms';
import { useAppState } from 'shared/state';
import { useNotistack, useUpdateEntities } from 'shared/hooks';
import StaffFormElement from './StaffFormElement';
type Props = {
	onSuccess: () => void;
	entity: any;
};
function getRole(role: string) {
	switch (role) {
		case 'member': {
			return { label: 'Member', value: 'member' };
		}
		case 'admin': {
			return { label: 'Admin', value: 'admin' };
		}
		case 'manager': {
			return { label: 'Manager', value: 'manager' };
		}
		default:
			return null;
	}
}

export default function EditStaffForm({ entity, onSuccess }: Props) {
	const { companyId } = useAppState();
	const { updateEntities } = useUpdateEntities();

	const { showNotification } = useNotistack();
	if (!entity) {
		return null;
	}

	return (
		<div>
			{companyId ? (
				<EntityForm.Main
					entity="staffEmployee"
					name={`staffEmployee-${companyId}`}
					url={`${companyId}/employee/${entity.id}/`}
					method="patch"
					id={entity.id}
					primaryKey="id"
					sendAsFormData={false}
					normalizeData={(data: any) => data}
					updateData
					fields={[
						{
							name: 'avatar',
							required: false,
							value: entity?.avatar,
							onSubmitKey: '',
							type: 'object',
							onSubmitValue: (value) => (value ? value : null)
						},
						{
							name: 'first_name',
							required: true,
							value: entity?.first_name,
							onSubmitKey: ''
						},
						{
							name: 'username',
							required: true,
							value: entity?.username,
							onSubmitKey: ''
						},
						{
							name: 'last_name',
							required: true,
							value: entity?.last_name,
							onSubmitKey: ''
						},
						{
							name: 'mobile',
							required: true,
							value: entity?.mobile,
							onSubmitKey: ''
						},
						{
							name: 'roles',
							required: true,
							value: getRole(entity?.roles[0]),
							type: 'object',
							onSubmitKey: '',
							onSubmitValue: (value) => [value.value]
						},
						{
							name: 'departments',
							onSubmitValue: (value) =>
								value.reduce((acc: any, curr: any) => [...acc, { id: curr.value }], []),
							required: true,
							type: 'array',
							onSubmitKey: ''
						},
						{
							name: 'can_send_news',
							required: false,
							value: true,
							onSubmitKey: ''
						}
					]}
					onSuccess={(response, resetForm): any => {
						showNotification({ message: 'Success! ' });
						updateEntities({
							entity: 'staffEmployee',
							entityId: entity?.id,
							updatingData: {
								departments: response?.departments
							}
						});
						resetForm();
						onSuccess();
					}}
					onError={(error, resetForm): any => {
						showNotification({
							message: error?.response?.data?.message ?? 'Something went wrong',
							variant: 'error'
						});

						resetForm();
					}}
				>
					{({ isSubmitting, values, setFieldValue, errors, setFieldError, resetForm }) => {
						return (
							<StaffFormElement
								values={values}
								setFieldValue={setFieldValue}
								setFieldError={setFieldError}
								isSubmitting={isSubmitting}
								errors={errors}
								closeModal={onSuccess}
								resetForm={resetForm}
								departments={entity?.departments}
								isCreate={false}
								isBeingEditedUserOwner={entity.is_business_owner}
							/>
						);
					}}
				</EntityForm.Main>
			) : null}
		</div>
	);
}

EditStaffForm.defaultProps = {
	onSuccess: () => {}
};
