import React, { useEffect, useState } from 'react';
import { Field } from 'formik';
import { KntInputField, KntPhoneInputField, SelectField } from 'shared/components/Fields';
import { FileUploader, KntButton, Loader } from 'shared/components';
import { EntytyFormInstanceProps } from 'shared/typings/formik.types';
import { KntTwilioStyle } from 'shared/services';
import { isMobile } from 'react-device-detect';
import { useAppState } from 'shared/state';
import TreeSelect, { SHOW_PARENT } from 'rc-tree-select';
import get from 'lodash/get';
import { AiOutlineCloseCircle } from 'react-icons/ai';
import config from 'config';

import EntityContainer from 'modules/entity/containers';
import { convertToTreeSelectData } from '../utils/convertsTreeSelectData';
import { useSwitch } from 'shared/hooks';
import { useAuth } from '../../../modules/auth/hooks/useAuth';
import clsx from 'clsx';

const imgPath = `${config.PUBLIC_ASSETS}img`;

type Props = {
	isCreate?: boolean;
	departments?: any | null;
	closeModal: () => void;
	id?: number;
	values: any;
	formValuesSetter?: (fieldValues: string[]) => void;
	isBeingEditedUserOwner?: boolean;
} & EntytyFormInstanceProps;

const rolesOptions = [
	{ label: 'Admin', value: 'admin' },
	{ label: 'Manager', value: 'manager' },
	{ label: 'Member', value: 'member' }
];

export default function StaffFormElement({
	values,
	setFieldValue,
	setFieldError,
	isSubmitting,
	errors,
	closeModal,
	resetForm,
	isCreate,
	departments,
	formValuesSetter = () => {},
	isBeingEditedUserOwner = false
}: Props) {
	const { companyId, isDarkMode } = useAppState();
	const { company, userData } = useAuth();
	const { checked, handleChange } = useSwitch({
		canSend: isCreate ? false : values.can_send_news
	});

	const [uploadMeta, setUploadMeta] = useState<any>(null);
	const [cancelSending, setCancelSending] = useState<any>(false);
	const [avatar, setAvatar] = useState(isCreate ? values?.avatar : null);

	const isBusinessOwner = userData.id === company?.company?.owner?.id;

	const [deps, setDeps] = useState({
		clientDeps: departments
			? departments.reduce((acc: any, curr: any) => {
					if (!curr.is_staff) {
						return [...acc, { value: curr.id, label: curr.name }];
					} else {
						return [...acc];
					}
			  }, [])
			: [],
		staffDeps: departments
			? departments.reduce((acc: any, curr: any) => {
					if (curr.is_staff) {
						return [...acc, { value: curr.id, label: curr.name }];
					} else {
						return [...acc];
					}
			  }, [])
			: []
	});

	let treeDataStaff = [];
	let treeDataClient = [];

	useEffect(() => {
		if (avatar) setFieldValue('avatar', avatar);
	}, [avatar]);

	const { clientDeps, staffDeps } = deps;

	useEffect(() => {
		setFieldValue('departments', [...clientDeps, ...staffDeps]);
	}, [deps]);

	useEffect(() => {
		if (isCreate) {
			const checkableFields = ['first_name', 'username', 'last_name', 'mobile', 'departments'];

			const fieldValues = Object.keys(values).filter((field) => {
				const fieldHasValue = Array.isArray(values[field]) ? values[field].length : values[field];
				if (fieldHasValue && checkableFields.includes(field)) {
					return values[field];
				}
			});
			formValuesSetter(fieldValues);
		}
	}, [values]);

	useEffect(() => {
		setFieldValue('can_send_news', checked.canSend);
	}, [checked.canSend]);

	return (
		<div id={'department-form'}>
			<div className="branch-form__header flex-center-between -mt-5 border-b border-kgrey-dark-1/30 px-6 py-4">
				<div className="branch-form__heading font-base font-medium">{`${
					isCreate ? 'Create new' : 'Edit'
				} staff`}</div>
				{isCreate ? (
					<div
						className="branch-form__reset cursor-pointer text-sm text-kgrey transition hover:text-kdark"
						onClick={resetForm}
					>
						Clear
					</div>
				) : null}
			</div>
			<div className=" w-100 p-6">
				<div className="mb-2 ml-2 text-sm font-light text-kgrey-menuText">Profile photo</div>
				<div className="mb-5 flex items-center">
					<div className="user-avatar-box__display flex-centered relative  h-20 w-20 overflow-hidden rounded-full dark:bg-kdark-lighter">
						<img
							src={
								isCreate
									? get(avatar, 'thumbnail_150') || `${imgPath}/chat-placeholder.png`
									: get(values?.avatar, 'thumbnail_150') || `${imgPath}/chat-placeholder.png`
							}
							alt=""
							className={'object-fit'}
						/>
						{uploadMeta !== null && (
							<div
								className={
									'absolute top-1/2 left-1/2 z-10 h-10 w-10 -translate-y-1/2 -translate-x-1/2'
								}
							>
								<div className="upload-media-spinner">
									<Loader variant="determinate" value={uploadMeta} />
								</div>
								<div
									onClick={() => {
										setCancelSending(true);
										setUploadMeta(null);
									}}
									className={
										'absolute top-1/2 left-1/2 z-20 h-8 w-8 -translate-y-1/2 -translate-x-1/2 ' +
										'cursor-pointer text-3xl text-primary-dark transition hover:text-primary'
									}
								>
									<AiOutlineCloseCircle />
								</div>
							</div>
						)}
					</div>
					<div className="ml-5">
						<FileUploader
							uploadMeta={uploadMeta}
							setUploadMeta={setUploadMeta}
							cancelSending={cancelSending}
							setCancelSending={setCancelSending}
							setFile={setAvatar}
						/>
					</div>
				</div>

				<Field
					label="First name:"
					name="first_name"
					component={KntInputField}
					placeholder="Enter First name"
				/>
				<Field
					label="Last name"
					name="last_name"
					component={KntInputField}
					placeholder="Enter Last name"
				/>
				<Field
					type="email"
					label="Email"
					name="username"
					component={KntInputField}
					placeholder="Enter Email"
				/>

				<Field
					label="Phone number"
					name="mobile"
					component={KntPhoneInputField}
					placeholder="Enter company mobile phone number"
					enableSearch
					onChangeHandler={setFieldValue}
				/>
				<div className="row align-user-start remove-inner-label mb-3.5">
					<div className="mb-2 ml-2 text-sm font-light text-kgrey-menuText">Staff Departments</div>
					<EntityContainer.All
						entity="tree"
						name={`AllTreeDepartments-Staff-${companyId}`}
						url={`/${companyId}/departments/tree/`}
						params={{
							limit: 100,
							extra: {
								is_staff: 'True'
							}
						}}
					>
						{({ items, isFetched }) => {
							treeDataStaff = isFetched && convertToTreeSelectData(items);
							return (
								<>
									{isFetched ? (
										<div>
											<TreeSelect
												getPopupContainer={(triggerNode: any) => triggerNode.parentNode}
												className={clsx('w-full', { 'tree-select-dark-mode': isDarkMode })}
												transitionName="rc-tree-select-dropdown-slide-up"
												choiceTransitionName="rc-tree-select-selection__choice-zoom"
												dropdownStyle={{ overflow: 'auto' }}
												dropdownPopupAlign={{
													overflow: { adjustY: 0, adjustX: 0 },
													offset: [0, 2]
												}}
												placeholder={<i>Select Staff Departments</i>}
												searchPlaceholder="please search"
												treeLine
												maxTagTextLength={10}
												multiple
												allowClear
												autoClearSearchValue
												treeData={treeDataStaff}
												treeNodeFilterProp="title"
												treeCheckable
												treeCheckStrictly
												treeDefaultExpandAll={!isCreate}
												showCheckedStrategy={SHOW_PARENT}
												onChange={(value: any) => {
													setDeps((prev) => ({ ...prev, staffDeps: value }));
												}}
												{...(isCreate ? null : { value: [...staffDeps] })}
											/>
											{/*{!staffDeps?.length ? (*/}
											{/*	<ErrorMessage name="departments">*/}
											{/*		{(message) => (*/}
											{/*			<div className="mt-0.5 text-xs text-red-500">{message}</div>*/}
											{/*		)}*/}
											{/*	</ErrorMessage>*/}
											{/*) : null}*/}
											{/*{!staffDeps?.length && !errors?.departments ? (
												<p className="mt-0.5 text-xs text-red-500">
													Select at least one staff department
												</p>
											) : null}*/}
										</div>
									) : (
										<div className="flex justify-center">
											<Loader />
										</div>
									)}
								</>
							);
						}}
					</EntityContainer.All>
				</div>
				<div className="mb-3.5">
					<div className="mb-2 ml-2 text-sm font-light text-kgrey-menuText">Client Departments</div>
					<EntityContainer.All
						entity="tree"
						name={`AllTreeDepartments-Client-${companyId}`}
						url={`/${companyId}/departments/tree/`}
						params={{
							limit: 100,
							extra: {
								is_staff: 'False'
							}
						}}
					>
						{({ items, isFetched }) => {
							treeDataClient = isFetched && convertToTreeSelectData(items);
							return (
								<>
									{isFetched ? (
										<div>
											<TreeSelect
												getPopupContainer={(triggerNode: any) => triggerNode.parentNode}
												className={clsx('w-full', { 'tree-select-dark-mode': isDarkMode })}
												transitionName="rc-tree-select-dropdown-slide-up"
												choiceTransitionName="rc-tree-select-selection__choice-zoom"
												dropdownStyle={{ overflow: 'auto' }}
												dropdownPopupAlign={{
													overflow: { adjustY: 0, adjustX: 0 },
													offset: [0, 2]
												}}
												placeholder={<i>Select Ð¡lient Departments</i>}
												searchPlaceholder="please search"
												treeLine
												maxTagTextLength={10}
												multiple
												allowClear
												autoClearSearchValue
												treeData={treeDataClient}
												treeNodeFilterProp="title"
												treeCheckable
												treeCheckStrictly
												treeDefaultExpandAll={!isCreate}
												showCheckedStrategy={SHOW_PARENT}
												onChange={(value: any) => {
													setDeps((prev) => ({ ...prev, clientDeps: value }));
												}}
												{...(isCreate ? null : { value: [...clientDeps] })}
											/>

											{/*{!clientDeps?.length ? (
												<ErrorMessage name="departments">
													{(message) => (
														<div className="mt-0.5 text-xs text-red-500">{message}</div>
													)}
												</ErrorMessage>
											) : null}*/}
										</div>
									) : (
										<div className="flex justify-center">
											<Loader />
										</div>
									)}
								</>
							);
						}}
					</EntityContainer.All>
				</div>
				<div className="mb-3.5">
					<div className="mb-2 ml-2 text-sm font-light text-kgrey-menuText">Roles</div>

					<Field
						component={SelectField}
						styles={KntTwilioStyle}
						name="roles"
						defaultValue={rolesOptions[2]}
						options={[
							...rolesOptions,
							...(isBusinessOwner ? [{ label: 'Owner', value: 'owner' }] : [])
						]}
						isSearchable={!isMobile}
						isDisabled={isBeingEditedUserOwner}
					/>
				</div>
			</div>

			<div className="branch-form__footer flex justify-end px-6 pt-4 pb-9">
				<KntButton
					className={'mr-5 border border-kgrey text-kgrey'}
					color="default"
					onClick={closeModal}
				>
					Cancel
				</KntButton>
				<KntButton
					className={'dark:text-white dark:hover:bg-primary-300 dark:focus:ring-primary-300'}
					type="submit"
					isSubmitting={isSubmitting}
					color="primary"
					isDisabled={Boolean(errors?.departments)}
				>
					{isCreate ? 'Create' : 'Update'}
				</KntButton>
			</div>
			{isSubmitting ? (
				<div className="absolute inset-0 flex items-center justify-center bg-white bg-opacity-70">
					<Loader />
				</div>
			) : null}
		</div>
	);
}

StaffFormElement.defaultProps = {
	isCreate: true,
	department: null,
	// eslint-disable-next-line @typescript-eslint/no-empty-function
	resetForm: () => {}
};
