import {
	useAxios,
	useEntityActionPopup,
	useNotistack,
	usePageChangeAfterEntityDelete,
	useQueryParams,
	useUpdateEntities
} from '../../../shared/hooks';
import { useAppState } from '../../../shared/state';
import { formRequest } from '../../../modules/entity/actions';
import { useDispatch } from 'react-redux';
import { useCallback, useEffect, useMemo, useState } from 'react';
import { CompSubscriptionType } from '../../../modules/user/reducers';
import { useAuth } from '../../../modules/auth/hooks/useAuth';
import { decrementEntityTotal } from 'modules/entity/actions';
import { StaffReAssignmentActionType } from '../components/StaffManagementTable';

export function useStaffManagementList(fetchStaffCountArg = () => {}) {
	const { companyId } = useAppState();
	const { companies } = useAuth();
	const dispatch = useDispatch();
	const { showNotification } = useNotistack();
	const { updateEntities } = useUpdateEntities();
	const { changeUrlParams, removeMultipleParams } = useQueryParams();
	const [reAssignmentActionType, setReAssignmentActionType] =
		useState<StaffReAssignmentActionType>('re_assign');

	const { forwardToPreviousPage } = usePageChangeAfterEntityDelete({
		pageKey: 'page',
		entity: 'staffEmployee',
		entityName: `staffEmployee-${companyId}`
	});

	const [paidMemberCount, setPaidMemberCount] = useState(0);
	const {
		visible: deleteVisible,
		showVisible: showDeleteVisible,
		loading: deleteLoading,
		toggleLoading: toggleDeleteLoading,
		closePopup: closeDeletePopup
	} = useEntityActionPopup<any>();

	const {
		visible: editVisible,
		showVisible: showEditVisible,
		closePopup: closeEditPopup
	} = useEntityActionPopup<any>();

	const {
		visible: reAssignModalVisible,
		showVisible: showReAssignModal,
		closePopup: closeReAssignModal
	} = useEntityActionPopup<any>();

	const {
		visible: reAssignRequiredWarningVisible,
		showVisible: showReAssignRequiredWarningVisible,
		closePopup: closeReAssignRequiredWarningVisible
	} = useEntityActionPopup<any>();

	const staffFetchUrl = useMemo(() => `/${companyId}/companies/staffs_count/`, [companyId]);

	//Request for fetching staff count in the business
	const {
		fetchData: fetchStaffCount,
		response: staffCountResponse,
		loading: staffCountLoading
	} = useAxios({
		url: staffFetchUrl
	});

	//Fetch Staff Count
	useEffect(() => {
		if (companyId) {
			(async () => {
				await fetchStaffCount();
			})();
		}
	}, [companyId]);

	// useEffect(() => {
	// 	if (staffCountResponse) {
	// 		console.log('staffCountResponse', staffCountResponse);
	// 	}
	// }, [staffCountResponse]);

	useEffect(() => {
		if (companyId && companies.length) {
			const currentCompany = companies.find((com) => com.company.id === companyId);
			const currentSubscription = currentCompany?.company.comp_subscription as CompSubscriptionType;
			if (currentSubscription) {
				setPaidMemberCount(currentSubscription.paid_staffs_count!);
			}
		}
	}, [companies, companyId]);

	const clickReAssignButtonHandler = useCallback((item) => {
		changeUrlParams({
			reAssign: 'true'
		});

		showReAssignModal(item);
	}, []);

	const closeReAssignModalHandler = useCallback(() => {
		removeMultipleParams(['reAssign']);
		closeReAssignModal();
	}, []);

	const updateStaffEntity = () => {
		updateEntities({
			entity: 'staffEmployee',
			entityId: deleteVisible?.id,
			updatingData: {
				roles: []
			}
		});
	};

	const handleClientDelete = (staff: any) => {
		//show loader
		toggleDeleteLoading();
		dispatch(
			formRequest({
				entity: 'staffEmployee',
				name: `staffEmployee-${companyId}`,
				method: 'delete',
				url: `/${companyId}/employee/${staff.id}/`,
				deleteData: true,
				id: staff.id,
				cb: {
					success: () => {
						showNotification({ message: 'Successfully deleted', variant: 'success' });
						dispatch(decrementEntityTotal('staffEmployee', `staffEmployee-${companyId}`));
						updateStaffEntity();

						//Decrement staff count deletion
						fetchStaffCountArg();

						forwardToPreviousPage();
					},
					error: (err) => {
						showNotification({ message: err?.response?.data?.message, variant: 'error' });
					},
					finally: () => {
						toggleDeleteLoading();
						closeDeletePopup();
					}
				},
				params: {},
				primaryKey: 'id',
				values: {}
			})
		);
	};

	return {
		deleteLoading,
		handleClientDelete,
		showDeleteVisible,
		closeDeletePopup,
		deleteVisible,
		editVisible,
		showEditVisible,
		closeEditPopup,
		staffCountLoading,
		staffCountResponse,
		paidMemberCount,
		fetchStaffCount,
		reAssignModalVisible,
		clickReAssignButtonHandler,
		closeReAssignModalHandler,
		reAssignmentActionType,
		setReAssignmentActionType,
		reAssignRequiredWarningVisible,
		showReAssignRequiredWarningVisible,
		closeReAssignRequiredWarningVisible
	};
}
